<template>
  <div style="overflow-y: hidden; height: 100%;">
    <div :style="{height: specHeight + 'px'}" style="width: 100%;"></div>
    <mt-header style="padding: 0px;" title="视频监控">
      <router-link to="/" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <div @click="clickHandle">
      <mt-cell style="height: 50px;" is-link>
        <span slot="title">{{projname}}</span>
      </mt-cell>
    </div>
    <div class="vedio_zone">
      <div class="menu-wrapper">
        <ul style="padding-left: 0px;">
          <li v-for="(item,index) in realZones" :key="item.AreaID" class="menu-item" :class="{'current': currentIndex===index}" @click="selectMenu(index)">
            <span class="text">
              {{item.AreaName}}
            </span>
          </li>
        </ul>
      </div>
      <div class="item-wrapper">
        <el-row :gutter="10" style="margin: 10px 10px 10px 10px;">
          <el-col style="margin-bottom: 10px;" :span="12" v-for="item in realtableData" :key="item.CameraNO">
            <el-card @click.native="entryscreen(item.VideoUrl)" style="height: 160px;" :body-style="{ padding: '0px' }">
              <img :onerror="defaultImg" :src="item.CoverUrl" style="width: 100%;" class="image">
              <div style="padding: 14px;">
                <span>{{item.CameraName}}</span>
              </div>
            </el-card>
          </el-col>
        </el-row>
      </div>
    </div>
    <mt-popup style="width: 100%" v-model="sheetVisible" position="bottom" class="mint-popup">
      <mt-picker style="width: 100%" :slots="projData" value-key="ProjectName" @change="onValuesChange"></mt-picker>
    </mt-popup>
  </div>
</template>
<script>
import { formatPath } from '@/utils'
export default {
  computed: {
    currentIndex () {
      return this.index
    },
    projData () {
      let projSlot = [{
        flex: 1,
        textAlign: 'center',
        values: this.$store.getters.projName,
        className: 'slot1'
      }]
      return projSlot
    },
    realtableData () {
      return this.tableData
    },
    realZones () {
      return this.zones
    }
  },
  data () {
    return {
      projKey: this.$store.getters.projName[0].ProjectNO.toString(),
      specHeight: this.$store.getters.statusBarHeight,
      index: 0,
      popupVisible: false,
      sheetVisible: false,
      projname: this.$store.getters.projName[0].ProjectName,
      tableData: {},
      zones: {},
      accessToken: '',
      defaultImg: 'this.src="static/img/defaultImg.jpg"'
    }
  },
  mounted () {
    var that = this
    this.$store.getters.projName.forEach(element => {
      this.$store.dispatch('GetVedioListInfo', element.ProjectNO).then(res => {
        if (that.$store.getters.vedioList[that.projKey] && that.$store.getters.vedioList[that.projKey][that.currentIndex].VideoList) {
          that.tableData = that.$store.getters.vedioList[that.projKey][that.currentIndex].VideoList
        }
        that.zones = that.$store.getters.vedioList[that.projKey]
      }).catch((err) => {
        console.log(err)
      })
    })
    this.$store.dispatch('GetVideoAccessToken').then((response) => {
      that.accessToken = response.AccessToken
    }).catch((err) => {
      console.log(err)
    })
  },
  methods: {
    testclick () {
      this.$store.getters.projName.forEach(element => {
        this.$store.dispatch('GetVedioListInfo', element.ProjectNO).then(res => {
          if (this.$store.getters.vedioList[this.projKey] && this.$store.getters.vedioList[this.projKey][this.currentIndex].VideoList) {
            this.tableData = this.$store.getters.vedioList[this.projKey][this.currentIndex].VideoList
          }
          this.zones = this.$store.getters.vedioList[this.projKey]
        }).catch((err) => {
          console.log(err)
        })
      })
    },
    entryscreen (url) {
      var jsonObj = {methodname: 'entryVideoScreen', AccessToken: this.accessToken, videoUrl: url}
      window.plugins.CustomBridgePlugin.js2android(jsonObj, null, null)
    },
    selectMenu (index) {
      this.index = index
      var that = this
      if (that.$store.getters.vedioList[that.projKey] && that.$store.getters.vedioList[that.projKey][that.currentIndex].VideoList) {
        that.tableData = that.$store.getters.vedioList[that.projKey][that.currentIndex].VideoList
      }
      that.zones = that.$store.getters.vedioList[that.projKey]
    },
    tranlatePath (path) {
      return formatPath(path)
    },
    onValuesChange (picker, values) {
      if (values[0] > values[1]) {
        picker.setSlotValue(1, values[0].ProjectName)
      }
      console.log(picker.getValues())
      this.projname = picker.getValues()[0].ProjectName
      this.projKey = picker.getValues()[0].ProjectNO.toString()
      var that = this
      if (that.$store.getters.vedioList[that.projKey] && that.$store.getters.vedioList[that.projKey][that.currentIndex].VideoList) {
        that.tableData = that.$store.getters.vedioList[that.projKey][that.currentIndex].VideoList
      } else {
        that.tableData = []
      }
      that.zones = that.$store.getters.vedioList[that.projKey]
    },
    clickHandle () {
      this.sheetVisible = !this.sheetVisible
    }
  }
}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
  .vedio_zone {
    display: flex;
    position: relative;
    width: 100%;
    height: 80%;
    overflow: hidden;
    .menu-wrapper {
      flex: 0 0 80px;
      width: 80px;
      background: #f3f5f7;
      .current {
        position: relative;
        z-index: 10;
        margin-top: -1px;
        background: #fff;
        font-weight: 700;
      }
      .text {
        display: table-cell;
        width: 56px;
        vertical-align: middle;
        font-size: 12px;
      }
      .menu-item {
        display: table;
        height: 54px;
        width: 100%;
        padding: 0 12px;
        line-height: 14px;
        .icon {
          display: inline-block;
          vertical-align: top;
          width: 12px;
          height: 12px;
          margin-right: 2px;
          background-size: 12px 12px;
          background-repeat: no-repeat;
        }
      }
    }
    .item-wrapper {
      flex: 1;
      overflow-y: scroll;
      .title {
        padding-left: 14px;
        height: 26px;
        line-height: 26px;
        border-left: 2px solid #d9dde1;
        font-size: 12px;
        color: rgb(147, 153, 159);
        background: #f3f5f7;
      }
      .food-item {
        display: flex;
        margin: 18px;
        padding-bottom: 18px;
      }
    }
  }
</style>



